@charset "UTF-8";
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  min-height: 100vh; }

* {
  box-sizing: border-box; }

#root {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 100vh; }
  #root > * {
    width: 100%; }

h1 {
  margin: 0; }

h2 {
  font-weight: 300; }

a {
  text-decoration: none; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

header label {
  display: block;
  margin-top: 10px; }

header,
footer {
  flex: 0 0 auto;
  padding: 40px 0; }

section {
  flex: 1 1 auto; }

.list {
  display: inline-block;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.12);
  border-radius: 4px;
  padding: 12px 10px; }
  .list .cell {
    padding: 0 14px;
    display: inline-block;
    position: relative; }
    .list .cell + .cell:before {
      content: "|";
      position: absolute;
      left: -6px;
      color: rgba(192, 192, 192, 0.226); }
    .list .cell.is-dead {
      color: rgba(192, 192, 192, 0.7); }
    .list .cell.is-left:after {
      content: "‚òùÔ∏è";
      position: absolute;
      left: 2px;
      top: 20px;
      font-size: 24px; }
    .list .cell.is-right:after {
      content: "üëÜ";
      position: absolute;
      left: 8px;
      top: 20px;
      font-size: 24px; }
    .list .cell.is-mid:after {
      content: "üßê";
      position: absolute;
      left: 8px;
      top: 20px;
      font-size: 24px; }
    .list .cell.is-mid.is-match:after {
      content: "üéâ"; }

.binary-search {
  display: inline-block;
  width: 75%; }
  .binary-search .input {
    margin-bottom: 40px; }
  .binary-search .binary-search {
    width: 100%; }
  .binary-search .result {
    padding-top: 30px; }
